.menu {
  box-sizing: border-box;

  height: 100vh;
  background: #e5e6e8;
  font-weight: 700;
  text-transform: uppercase;

  &__top {
    margin-bottom: 32px;

    @include onTablet {
      margin-bottom: 0;
    }
  }
  
  &__nav {
    margin-bottom: 32px;
  }
  
  &__phone-number {
    display: block;
    padding-bottom: 16px;
  
    font-size: 16px;
    line-height: 140%;
    letter-spacing: 2px;
    color: inherit;
    text-decoration: none;
  }
  
  &__call-to-order {
    display: block;
    width: fit-content;
  
    padding-bottom: 3px;
    border-bottom: 1px solid $main-text-color;
  
    font-size: 16px;
    line-height: 140%;
    letter-spacing: 2px;
    color: inherit;
    text-decoration: none;
  
  }
}
