.button {
  cursor: pointer;
  width: 100%;
  height: 56px;
  background-color: black;
  color: white;
  border-radius: 8px;

  &:hover {
    color: #292929;
    border-radius: 8px;
    border: 2px solid #f0f1f4;
    background: #f8f8fa;
  }

  &:active {
    color: #fff;
    border-radius: 8px;
    background: #111;
  }
}
